.register {
    height: calc(90vh - 50px);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 60px;
    justify-content: center;


  }
  
  .registerTitle {
    font-size: 50px;
  }
  
  .registerForm {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
  }
  
  .registerForm > label {
    margin: 10px 0;
  }
  
  .registerInput {
    padding: 10px;
    background-color: white;
    border: none;
    border-radius: 10px;
  }
  
  .registerInput:focus {
    outline: none;
  }
  
  .registerButton {
    margin-top: 20px;
    cursor: pointer;
    background-color: teal;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 10px;
    text-align: center;
  }
  
  .registerLoginButton {
    position: absolute;
    top: 60px;
    right: 20px;
    background-color: lightcoral;
    cursor: pointer;
    padding: 10px;
    border: none;
    color: white;
    border-radius: 10px;
  }
  @media screen and (max-width: 992px) {
    .register {
      height: calc(90vh - 50px);
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-top: 60px;
      justify-content: center;

    }
    
